import React from 'react';

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'));
const ChapterList = React.lazy(() => import('./views/Components/chapter/ChapterList'));
const AddChapter = React.lazy(() => import('./views/Components/chapter/AddChapter'));

const TopicsList = React.lazy(() => import('./views/Components/topics/TopicsList'));
const AddTopics = React.lazy(() => import('./views/Components/topics/AddTopics'));
const EditTopics = React.lazy(() => import('./views/Components/topics/EditTopics'));

const UnitList = React.lazy(() => import('./views/Components/unit/UnitList'));
const AddUnit = React.lazy(() => import('./views/Components/unit/AddUnit'));
const EditUnit = React.lazy(() => import('./views/Components/unit/EditUnit'));

const UserList = React.lazy(() => import('./views/Components/user/UserList'));
const AddUser = React.lazy(() => import('./views/Components/user/AddUser'));

const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard, exact: true, },
  //chapter
  { path: '/chapter', name: 'Chapter', component: ChapterList, exact: true, },
  { path: '/chapter/add', name: 'addChapter', component: AddChapter, exact: true, },
  //topics
  { path: '/chapter/:id/topics', name: 'Topic', component: TopicsList, exact: true, },
  { path: '/chapter/:id/topics/add', name: 'addTopic', component: AddTopics, exact: true, },
  { path: '/chapter/:chapterId/topics/edit/:id', name: 'editTopic', component: EditTopics, exact: true, },
  //units
  { path: '/chapter/:chapterId/topics/:id/unit', name: 'Unit', component: UnitList, exact: true, },
  { path: '/chapter/:chapterId/topics/:id/unit/add', name: 'addUnit', component: AddUnit, exact: true, },
  { path: '/chapter/:chapterId/topics/:topicsId/unit/edit/:id', name: 'editUnit', component: EditUnit, exact: true, },
  //user
  { path: '/user', name: 'Unit', component: UserList, exact: true, },
  { path: '/user/add', name: 'addUnit', component: AddUser, exact: true, },
];

export default routes;
